@media screen and (min-width: 1000px) {
  img {
    max-width: 4%;
    height: auto;
    margin: "20px";
  }

  .Coin-Logo {
    min-width: 30px;
    height: auto;
    position: relative;
    padding-left: 12px;
    top: 5px;
    right: 10px;
  }
  .symbol {
    position: relative;
    font-size: 100%;
    left: 10px;
    color: grey;
  }
  table {
    font-family: "Nunito", sans-serif;
    min-width: 85vw;
    max-width: 85vw;
    color: #FFFFFF;
    border-radius: 10px;
    border: 1px solid rgb(255, 255, 255);
    box-shadow: rgb(11, 11, 11) 1px 1px 0px inset;
    font-size: 14px;
    box-shadow: rgb(11, 11, 11) 1.5px 1.5px 3px,
      #5C68FF) -3px -3px 6px -0.5px;
    background: rgb(22, 22, 22);
    border-width: 0px;
    border-style: solid;
    border-color: rgb(22, 22, 22);
    border-image: initial;
    border-radius: 12px;
    overflow: hidden;
    table-layout: fixed;
    width: fixed;
    padding: 10px;
  }
  .chartandpay {
    table-layout: fixed;
    position: relative;
    left: 100px;
  }
  tr {
    text-align: center;
    padding: 12px;

    border-width: 1px;
    border-radius: 0px;
  }
  .theCoinId:hover {
    background-color: rgb(11, 11, 11);
    box-shadow: rgb(22, 22, 22) 1.5px 1.5px 3px,
      rgb(22, 22, 22) -3px -3px 6px -0.5px;
    cursor: pointer;
  }

  .theCoinIdSec {
    float: left;
  }
  .theCoinId {
    min-width: 1vw;
  }
  chart {
    width: 30px;
    float: left;
  }

  .table-header-button {
    cursor: pointer;
  }
  .table-header-button:hover {
    cursor: pointer;
    box-shadow: rgb(22, 22, 22) 1.5px 1.5px 3px,
      rgb(22, 22, 22) -3px -3px 6px -0.5px;
  }
  th,
  td,
  tr {
    text-align: left;
    padding: 10px;
    border-width: 1px;
    border-color: red;
    border: collapse;
    table-layout: fixed;

    border-radius: 10px;
  }
  td {
    margin: 0px solid black;

    border: collapse;
    margin: 0px;
  }

  .up {
    background-color: rgb(22, 22, 22);
    box-shadow: rgb(22, 22, 22) 1.5px 1.5px 3px,
      rgb(22, 22, 22) -3px -3px 6px -0.5px;
    border-collapse: collapse;
    border-radius: 10px;
    color: #00FF00;
    text-align: center;
    border: 1px solid rgb(22, 22, 22);
  }
  .down {
    background-color: rgb(22, 22, 22);
    box-shadow: rgb(22, 22, 22) 1.5px 1.5px 3px,
      rgb(22, 22, 22) -3px -3px 6px -0.5px;
    border-collapse: collapse;
    border-radius: 10px;
    color: #FF0D0D;
    text-align: center;
    border: 0px solid #FF0D0D;
  }
  .fuck {
    background-color: rgb(22, 22, 22);
    box-shadow: rgb(22, 22, 22) 1.5px 1.5px 3px,
      rgb(22, 22, 22) -3px -3px 6px -0.5px;
    border-collapse: collapse;
    border-radius: 10px;
    color: #FF0D0D;
    text-align: center;
    border: 0px solid rgb(255, 255, 255);
  }
  iframe {
    border-radius: 10px;

    float: right;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    border: 1px solid rgb(226, 207, 213);
    min-width: 40px;
    min-height: 40px;
  }
  .page-selection {
  }
  .emoji {
    font-size: 28px;
    position: relative;
    box-shadow: rgb(22, 22, 22) 1.5px 1.5px 3px,
      rgb(22, 22, 22) -3px -3px 6px -0.5px;

    background-color: rgb(22, 22, 22);
    border: 1px solid rgb(22, 22, 22);
  }
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    width: 40vw;
    margin: 0 auto;
    min-height: 1vh;
    padding-bottom: 5px;
  }
  .btn {
    flex: 1 1 auto;
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 10px;
    padding: 3px;
    text-align: center;

    transition: 0.5s;
    background-size: 200% auto;
    color: rgb(92, 91, 91);
    /* text-shadow: 0px 0px 10px rgba(0,0,0,0.2);*/
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    float: left;
  }

  /* Demo Stuff End -> */

  /* <- Magic Stuff Start */

  .btn:hover {
    background-color: rgb(21, 21, 21);
  }

  .btn-1 {
    color: #FFFFFF;
    font: 400 13.3333px Arial;
    font-family: Arial;
    font-weight: 700;
    padding: 7px;
    margin: 3px;
    max-width: 90px;
    box-shadow: rgb(33, 33, 33) 1.5px 1.5px 3px,
      rgb(33, 33, 33) -3px -3px 6px -0.5px;
    background: rgb(11, 11, 11);
    border-width: 0px;
    border-style: solid;
    border-color: rgb(226, 207, 213);
    border-image: initial;
    border-radius: 12px;
    cursor: pointer;
    overflow: hidden;
    float: left;
  }
  .btn-2 {
    color: rgb(91, 38, 57);
    font-family: "Nunito", sans-serif;
    padding: 5px;
    margin: 3px;
    max-width: 90px;
    box-shadow: rgb(247, 242, 244) 1px 1px 0px inset;
    background: rgb(44, 44, 44);
    border-width: 1px;
    border-style: solid;
    border-color: rgb(226, 207, 213);
    border-image: initial;
    border-radius: 12px;
    cursor: pointer;
    overflow: hidden;
    float: left;
  }
  .loader {
    font-size: 50px;
    animation: spin 2s linear infinite;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
